:root {
    --PrimaryColor: hsl(199, 100%, 33%);
    --SecondaryColor: hsl(187, 85%, 43%);
    --gradientColor: linear-gradient(to right, hsl(187, 85%, 43%), hsl(199, 100%, 33%));
    --whiteColor: hsl(0, 0%, 100%);
    --blackColor: hsl(201, 33%, 16%);
    --textColor: hsl(240, 4%, 36%);
    --whiteColorDeam: hsl(0, 0%, 93%);
    --greyText: rgb(190, 190, 190);
    --inputColor: rgb(239, 239, 239);
    --bodyColor: rgb(240, 240, 246);
    --cardBG: rgb(225, 225, 235);
    --cardHover: rgb(255, 255, 255);
  }
  
  .main {
    width: 100%;
  
    .secTitle {
      .title {
        position: relative;
        width: max-content;
        color: var(--textColor);
        margin: 1rem 0;
        z-index: 2;
  
        &::after {
          position: absolute;
          content: '';
          background: var(--SecondaryColor);
          height: 5px;
          width: 150px !important;
          right: 0;
          bottom: 2px;
          z-index: -1;
        }
      }
    }
  
    .secContent {
      display: grid; 
      justify-content: center;
      gap: 1.5rem;
  
      .singleDestination {
        height: 100%;
        display: grid;
        row-gap: 10px;
        border-radius: 10px;
        align-items: center;
        background: var(--cardBG);
        box-shadow: 0 2px 4px rgba(140, 140, 141, 0.549);
        overflow: hidden;
        &:hover {
          background: var(--cardHover);
          box-shadow: 1 4px 4px rgba(85, 85, 114, 0.549);
          transition: .3s ease;
        }
  
        .imageDiv {
          height: 235px;
          width: 100%;
          overflow: hidden;
  
          img {
            width: 100%;
            height: 100%;
            top: 0;
            object-fit: cover !important;
            transition: 2s ease;
          }
  
          &:hover {
            img {
              transform: scale(1.1);
            }
          }
        }
  
        .cardInfo {
          padding: 1rem;
  
          .desTitle {
            color: var(--blackColor);
            font-size: 1.25rem;
          }
  
          .continent {
            gap: 0.5rem;
  
            .icon {
              color: var(--textColor);
              font-size: 18px;
            }
  
            .name {
              color: var(--textColor);
              font-size: 14px;
            }
          }
  
          .fees {
            width: 100%;
            justify-content: space-between;
            padding: 1rem;
            margin: 1rem 0;
            border-top: 1.5px solid var(--greyText);
            border-bottom: 1.5px solid var(--greyText);
  
            .grade {
              color: var(--textColor);
              max-width: 100px;
              line-height: 20px;
              display: flex;
              align-items: center;
              justify-content: center;
  
              small {
                text-align: center;
                background: var(--gradientColor);
                border-radius: 1rem;
                color: var(--whiteColor);
                padding: 1px 10px;
                font-size: 10px;
              }
            }
  
            .price {
              font-size: 2rem;
              color: var(--blackColor);
            }
          }
  
          .desc {
            font-size: 13px;
            color: var(--textColor);
            font-weight: 500;
          }
  
          .btn {
            margin-top: 1rem;
            color: var(--whiteColor);
            font-weight: 600;
            justify-content: space-between;
            gap: 0.5rem;
  
            .icon {
              align-self: center;
              font-size: 18px;
              color: var(--whiteColor);
            }
          }
        }
      }
    }
  }
  

  // MEDIA QUERIES  ========================================>


 
@media screen and (min-width: 500px){
    .title{
        font-size: 1.5rem;
        &::after{
          width: 155px !important;
          
        }
    }

    .secContent{
        grid-template-columns: repeat(2, 1fr);

        .singleDestination{
            height: auto;
            ;
        }
    }
}

@media screen and (min-width: 840px){
  .secContent{
    grid-template-columns: repeat(3, 1fr);

    .singleDestination{
        height: auto;
    }
  }
}


